/* ===== ROOT ===== */

:root {
    --second-color: rgb(27, 26, 26);
    --text-color: #dbdbdb;
    --shadow-color: #05020c;
    --border-color: #4e4e4e66; 
    --title-font: 'Noto Sans', sans-serif;
    --text-font: 'Montserrat';
}

/* ===== ROOT ===== */

* {
    box-sizing: border-box;
    text-decoration: none;
}

html, body, #root { 
    height: 100%;
    background-color: #000000;
    margin: 0; 
}

button {
    border: none;
}

/* ===== CLASS GLOBAL ===== */

.main {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    height: 100%;
}

.containerApp {
    position: relative;
    width: 50%;
    border-left: 1px solid #4e4e4e66;
    border-right: 1px solid #4e4e4e66;
    box-shadow: rgba(40 27 51 / 25%) 0px 54px 55px,
    rgba(40 27 51 / 12%) 0px -12px 30px, 
    rgba(40 27 51 / 12%) 0px 4px 6px, 
    rgba(40 27 51 / 17%) 0px 12px 13px, 
    rgba(40 27 51 / 9%) 0px -3px 5px;
    z-index: 1;
}

.body {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}

.content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 100%;
}

.contentContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0% 10% 10% 10%;
    -webkit-animation: fadeIn 1s forwards;
            animation: fadeIn 1s forwards;
}


.imgContent {
    display: block;
}


.main > svg:first-of-type {
    position: absolute;
    color: #dbdbdb;
    color: var(--text-color);
    font-size: 48px;
    top: 80%;
    left: 24%;
    z-index: 6;
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
}

.main > p:first-of-type {
    position: absolute; 
    color: #dbdbdb; 
    color: var(--text-color);
    font-size: 21px;
    top: 75%;
    left: 17%;
}

.main > svg:last-of-type {
    position: absolute;
    color: #dbdbdb;
    color: var(--text-color);
    font-size: 48px;
    top: 20%;
    right: 24%;
    z-index: 6;
    -webkit-transform: rotate(216deg);
            transform: rotate(216deg);
}

.main > p:last-of-type {
    position: absolute; 
    color: #dbdbdb; 
    color: var(--text-color);
    font-size: 21px;
    top: 23%;
    right: 16%;
}


.imgSpace {
    height: 100%;
    width: 100%;
    -webkit-animation: fadeIn 3s forwards;
            animation: fadeIn 3s forwards;
}

@media screen and (max-width: 1023px) {
    .main > svg:first-of-type {
        display: none;
    }
    .main > svg:last-of-type {
        display: none;
    }
    .main > p:last-of-type {
        display: none;
    }
    .main > p:first-of-type {
        display: none;
    }
}

@media screen and (max-width: 1280px) {
    .main > svg:first-of-type {
        font-size: 38px;
    }
    .main > p:last-of-type {
        right: 13%;;
    }
    .main > svg:last-of-type {
        font-size: 38px;
        right: 23%; 
    }
    .main > p:first-of-type {
        left: 13%;
    }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0.9;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0.9;
  }
}

/* ===== STARTING ===== */

.wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
    width: 100%;
    -webkit-animation: fadeIn 1s forwards;
            animation: fadeIn 1s forwards;
    align-items: center;
  }
  
  .wrapper h1 {
    font-family: 'Home';
    font-size: 64px;
    color: #dbdbdb;
    color: var(--text-color);
    text-align: center;
    margin: 0;
  }
  
  .wrapper p {
    font-family: 'Noto Sans', sans-serif;
    font-family: var(--title-font);
    font-size: 24px;
    color: #dbdbdb;
    color: var(--text-color);
    text-align: center;
    margin: 0;
  }
  
  .wrapper h1 span {
    position: absolute;
  }
  
  .buttonPlay,
  .buttonPlay:after {
    font-family: 'Noto Sans', sans-serif;
    font-family: var(--title-font);
    font-size: 20px;
    color: #dbdbdb;
    color: var(--text-color);
    width: 150px;
    height: 76px;
    line-height: 78px;
    background: linear-gradient(45deg, transparent 5%, #FF013C 5%);
    border: 0;
    letter-spacing: 3px;
    box-shadow: 6px 0px 0px #00e6f6;
    outline: transparent;
    position: relative;
    cursor: pointer;
    align-self: center;
  }
  
  .buttonPlay:after {
    --slice-0: inset(50% 50% 50% 50%);
    --slice-1: inset(80% -6px 0 0);
    --slice-2: inset(50% -6px 30% 0);
    --slice-3: inset(10% -6px 85% 0);
    --slice-4: inset(40% -6px 43% 0);
    --slice-5: inset(80% -6px 5% 0);
      
    content: 'ALTERNATE TEXT';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg, transparent 3%, #00E6F6 3%, #00E6F6 5%, #FF013C 5%);
    text-shadow: -3px -3px 0px #F8F005, 3px 3px 0px #00E6F6;
    -webkit-clip-path: var(--slice-0);
            clip-path: var(--slice-0);
  }
  
  .buttonPlay:hover:after {
    -webkit-animation: 1s glitch;
            animation: 1s glitch;
    -webkit-animation-timing-function: steps(2, end);
            animation-timing-function: steps(2, end);
  }
    
  @-webkit-keyframes glitch {
    0% {
    -webkit-clip-path: var(--slice-1);
            clip-path: var(--slice-1);
    -webkit-transform: translate(-20px, -10px);
            transform: translate(-20px, -10px);
    }
    10% {
    -webkit-clip-path: var(--slice-3);
            clip-path: var(--slice-3);
    -webkit-transform: translate(10px, 10px);
            transform: translate(10px, 10px);
    }
    20% {
    -webkit-clip-path: var(--slice-1);
            clip-path: var(--slice-1);
    -webkit-transform: translate(-10px, 10px);
            transform: translate(-10px, 10px);
    }
    30% {
    -webkit-clip-path: var(--slice-3);
            clip-path: var(--slice-3);
    -webkit-transform: translate(0px, 5px);
            transform: translate(0px, 5px);
    }
    40% {
    -webkit-clip-path: var(--slice-2);
            clip-path: var(--slice-2);
    -webkit-transform: translate(-5px, 0px);
            transform: translate(-5px, 0px);
    }
    50% {
    -webkit-clip-path: var(--slice-3);
            clip-path: var(--slice-3);
    -webkit-transform: translate(5px, 0px);
            transform: translate(5px, 0px);
    }
    60% {
    -webkit-clip-path: var(--slice-4);
            clip-path: var(--slice-4);
    -webkit-transform: translate(5px, 10px);
            transform: translate(5px, 10px);
    }
    70% {
    -webkit-clip-path: var(--slice-2);
            clip-path: var(--slice-2);
    -webkit-transform: translate(-10px, 10px);
            transform: translate(-10px, 10px);
    }
    80% {
    -webkit-clip-path: var(--slice-5);
            clip-path: var(--slice-5);
    -webkit-transform: translate(20px, -10px);
            transform: translate(20px, -10px);
    }
    90% {
    -webkit-clip-path: var(--slice-1);
            clip-path: var(--slice-1);
    -webkit-transform: translate(-10px, 0px);
            transform: translate(-10px, 0px);
    }
    100% {
    -webkit-clip-path: var(--slice-1);
            clip-path: var(--slice-1);
    -webkit-transform: translate(0);
            transform: translate(0);
    }
  }
    
  @keyframes glitch {
    0% {
    -webkit-clip-path: var(--slice-1);
            clip-path: var(--slice-1);
    -webkit-transform: translate(-20px, -10px);
            transform: translate(-20px, -10px);
    }
    10% {
    -webkit-clip-path: var(--slice-3);
            clip-path: var(--slice-3);
    -webkit-transform: translate(10px, 10px);
            transform: translate(10px, 10px);
    }
    20% {
    -webkit-clip-path: var(--slice-1);
            clip-path: var(--slice-1);
    -webkit-transform: translate(-10px, 10px);
            transform: translate(-10px, 10px);
    }
    30% {
    -webkit-clip-path: var(--slice-3);
            clip-path: var(--slice-3);
    -webkit-transform: translate(0px, 5px);
            transform: translate(0px, 5px);
    }
    40% {
    -webkit-clip-path: var(--slice-2);
            clip-path: var(--slice-2);
    -webkit-transform: translate(-5px, 0px);
            transform: translate(-5px, 0px);
    }
    50% {
    -webkit-clip-path: var(--slice-3);
            clip-path: var(--slice-3);
    -webkit-transform: translate(5px, 0px);
            transform: translate(5px, 0px);
    }
    60% {
    -webkit-clip-path: var(--slice-4);
            clip-path: var(--slice-4);
    -webkit-transform: translate(5px, 10px);
            transform: translate(5px, 10px);
    }
    70% {
    -webkit-clip-path: var(--slice-2);
            clip-path: var(--slice-2);
    -webkit-transform: translate(-10px, 10px);
            transform: translate(-10px, 10px);
    }
    80% {
    -webkit-clip-path: var(--slice-5);
            clip-path: var(--slice-5);
    -webkit-transform: translate(20px, -10px);
            transform: translate(20px, -10px);
    }
    90% {
    -webkit-clip-path: var(--slice-1);
            clip-path: var(--slice-1);
    -webkit-transform: translate(-10px, 0px);
            transform: translate(-10px, 0px);
    }
    100% {
    -webkit-clip-path: var(--slice-1);
            clip-path: var(--slice-1);
    -webkit-transform: translate(0);
            transform: translate(0);
    }
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
    
  @media (min-width: 768px) {
    .buttonPlay,
    .buttonPlay:after {
      width: 180px;
      height: 70px;
      line-height: 71px;
    }
  }
  
