.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 2%;
    position: absolute;
    width: 25%;
    height: 50%;
    opacity: 0.9;
}

.header > img {
    width: 50%;
}

.header > p {
    font-family: var(--title-font);
    font-size: 24px;
    font-weight: 600;
    color: var(--text-color);
    cursor: default;
}

@media screen and (max-width: 1023px) {
    .header {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        width: auto;
        height: auto;
        align-items: flex-start;
        padding: 2%;
    }
    .header > img {
        display: none;
    }
    .header > p{
        display: none;
    }
}

@media screen and (max-width: 1440px) {
    .header > p {
        font-size: 16px;
    }
}
.footer {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    position: absolute;
    bottom: 10%;
    right: 0;
    width: 25%;
    height: 25%;
    opacity: 0.9;
    align-items: center;
}

.footer > h2 {
    font-family: var(--title-font);
    font-size: 24px;
    font-weight: 600;
    color: var(--text-color);
    text-align: center;
    margin: 0;
    cursor: default;
}

.footer > hr {
    width: 50%;
    color: var(--text-color);
    z-index: 5;
    height: 2px;
}

.socialIcons {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.socialIcons > a > svg {
    color: var(--text-color);
    padding: 15px;
    font-size: 30px;
    cursor: pointer;
    box-sizing: content-box;
    transition: 0.25s;
    background: none;
}

.socialIcons > a > svg:hover {
    color: #382351;
}

@media screen and (max-width: 1023px) {
    .footer {
        z-index: 2;
        bottom: auto;
        right: auto;
        width: 100%;
        height: auto;
        padding-top: 1%;
    }
    .footer > h2, hr {
        display: none;
    }
    .socialIcons > a > svg {
        font-size: 23px;
    }
}
